//
// UTILITIES / TYPE
//-----------------------------------------------------------------------

.u-text-quiet {
  color: $color-gray-2;

  a {
    color: $color-gray-2;
  }
}

.u-text-loud {
  font-weight: $text-font-weight-heavy;
}

.u-text-serif {
  font-family: $text-font-family-serif;
}

.u-text-primary {
  color: $color-brand-primary;
}


// SIZES
//---------------------------

.u-super {
  font-size: 4.8rem;

  @media #{$breakpoint-medium} {
    font-size: 8rem;
  }
}

.u-xlarge {
  font-size: 2.4rem;

  @media #{$breakpoint-medium} {
    font-size: 4rem;
  }
}

.u-large { // h1
  font-size: 2.4rem;

  @media #{$breakpoint-medium} {
    font-size: 3.2rem;
  }

}

.u-medium { // h2
  font-size: 1.8rem;

  @media #{$breakpoint-medium} {
    font-size: 2.4rem;
  }
}

.u-small { // h3
  font-size: 1.6rem;

  @media #{$breakpoint-medium} {
    font-size: 1.8rem;
  }
}

.u-xsmall { // P small
  font-size: $text-font-size-small; // 1.4rem

  @media #{$breakpoint-medium} {
    font-size: $text-font-size-small-md; // 1.6rem
  }
}

.u-tiny { // h4
  font-size: 1.2rem;

  @media #{$breakpoint-medium} {
    font-size: 1.4rem;
  }
}